import React,{useState,useEffect} from 'react'
import {storeFieldData} from '../include/FieldData'

function isPhoneNumber(number) {    
    if(number.match(/^([0]{1})([9]{1})([0-9]{9})$/))
        return true;    
    return false;    
}

function isEmail(email) {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

function acceptedFileType(file){
    let accepted=["application/pdf","image/png","image/jpeg","image/jpg"];
    if(accepted.indexOf(file.type) >-1 )
        return true;
    return false;
}

function InputBox(props){
    let [value,setValue]=useState('');
    let [error,setError]=useState('');

    useEffect(
        ()=>{
            let haveError=true;
            if(props.required && value==='')
                setError('پرکردن این فیلد ضروری است');            
            else if(props.isNumber && !value.match(/^([0-9]*)$/) )
                setError('فقط عدد صحیح قابل قبول است');
            else if(props.isMobile && !isPhoneNumber(value) )
                setError('شماره قابل قبول نیست');                
            else if(props.isEmail && !isEmail(value))
                setError('یک ایمیل معتبر وارد کنید');
            else if(props.type==='file' && value.size>1024000)
                setError('حجم فایل انتخاب شده زیاد است');
            else if(props.type==='file' && !acceptedFileType(value) && value!=='')
                setError('فرمت فایل انتخابی قابل قبول نمی باشد');
            else
                {
                    setError('');            
                    haveError=false;
                }                        
            
            storeFieldData(props.name,value,haveError);                        
        },[props.type,props.name,props.required, props.isNumber, props.isMobile,props.isEmail, value]);

    if(props.type ==='file')
    return(
        <div className="input-contaner">
            <label>{props.label}</label>            
            <input 
                type={props.type} name={props.name} id={props.name} placeholder={props.placeholder}
                onChange={(e)=>setValue(e.target.files[0])} />
                    <span className="someHolder">{props.type==='file'? props.placeholder:''}</span>
            <div className="error"><span>{error}</span></div>
        </div>
    );
    else if(props.type==='textarea')
    return(
        <div className="input-contaner">
            <label>{props.label}</label>            
            <textarea 
                type={props.type} name={props.name} id={props.name} placeholder={props.placeholder}
                    value={value} onChange={(e)=>setValue(e.target.value)} rows="4" />            
            <div className="error"><span>{error}</span></div>
        </div>
    );
    else
    return(
        <div className="input-contaner">
            <label>{props.label}</label>            
            <input 
                type={props.type} name={props.name} id={props.name} placeholder={props.placeholder} value={value}
                onChange={(e)=>setValue(e.target.value)} maxLength="128"/>
                    <span className="someHolder">{props.type==='file'? props.placeholder:''}</span>
            <div className="error"><span>{error}</span></div>
        </div>
    );
}

export default InputBox;