import React,{useState} from 'react'
import InputBox from './InputBox'
import {rest_urls} from '../constants'

function CaptchaCode(){
    const [src,setSrc]=useState(`${rest_urls.base+rest_urls.captcha}?v=${Math.random()*10}`)    
    
    return(
        <div className="w3-row captcha-code">            
            <div className="w3-col m8 s12">
                <InputBox type="text" name="captchaCode"  placeholder="کد امنیتی را وارد کنید" label="کد امنیتی" required={true} />
            </div>
            <div className="w3-col m4 s12 image-box">
                <div>
                    <img src={src} width="80px" alt="captchaCode"/>
                    <button onClick={(e)=>{
                        e.preventDefault();
                        setSrc(`${rest_urls.base+rest_urls.captcha}?v=${Math.random()*10}`);
                    }}>تغییر عکس</button>
                </div>
            </div>
        </div>
    );
}

export default CaptchaCode;