 var jsonData=[];
function storeFieldData(name,value,error){
    for(let i=0;i<jsonData.length;i++)
    {
        if(jsonData[i].fieldName===name)
        {
            jsonData[i].fieldValue=value;
            jsonData[i].haveError=error
            return;
        }
    }    
    
    jsonData.push({fieldName:name,fieldValue:value,haveError:error});
}

function getData(){
    return jsonData;
}
export {storeFieldData,getData};