import React from 'react'
import axios from 'axios'
import {getData} from '../include/FieldData'
import {rest_urls} from '../constants'


class OrderForm extends React.Component{
    constructor(props)
    {
        super(props);
        this.state={
            actionUrl:rest_urls.base+rest_urls.registerOrder,
            resultContent:'پس از تکمیل فرم بر روی ثبت کلیک کنید.',
            contentClass:''
        };
    }

    handleSubmit=async (e)=>{
        e.preventDefault();
        console.log('submit');
        this.setState({contentClass:''});
        this.setState({resultContent:'لطفا منتظر بمانید'});
        let data=getData();        
        const form = new FormData();
        for(let i=0;i<data.length;i++){
            if(data[i].haveError)
                {
                    this.setState({resultContent:'موارد خواسته شده را درست وارد کنید'});
                    this.setState({contentClass:'w3-text-orange'});
                    return;
                }            
            form.append(data[i].fieldName, data[i].fieldValue);
        }        
        
        await axios.post(this.state.actionUrl,form,{ withCredentials: true })
            .then(res =>{
                if(res.data.code==='success')
                    this.setState({contentClass:'w3-text-green'});
                else
                    this.setState({contentClass:'w3-text-red'});

                this.setState({resultContent:res.data.message});                
            })
            .catch(e=>{
                console.log(e);
                this.setState({contentClass:'w3-text-red'});
                this.setState({resultContent:'مشکل در ارتباط با سرور'});
            });

    }

    render(){
        let style=`w3-row form-result ${this.state.contentClass}`;
        return(
            <React.Fragment>
                <form className="w3-row" onSubmit={this.handleSubmit}>
                    {this.props.children}
                    
                </form>
                <div className={style}  >
                    {this.state.resultContent}
                </div>
            </React.Fragment>
        )
    }
}

export default OrderForm;